import {ShepherdTour, TourMethods} from 'react-shepherd';
import 'shepherd.js/dist/css/shepherd.css';
import TourStart from './TourStart';
import React from 'react';

const buttons = {
  cancel: {
    classes: 'shepherd-button-secondary',
    text: '',
    type: 'cancel'
  },
  back: {
    classes: 'shepherd-button-back',
    text: '',
    type: 'back'
  },
  next: {
    classes: 'btn btn-stretched',
    text: '',
    type: 'next'
  },
  complete: {
    classes: 'btn btn-stretched',
    text: '',
    type: 'complete'
  }
};

const stepDefault = {
  highlightClass: 'highlight',
  scrollTo: {
    behavior: 'smooth',
    block: 'center'
  },
  cancelIcon: false,
  modalOverlayOpeningPadding: 5,
  modalOverlayOpeningRadius: 5,
}

let steps = [
  {
    ...stepDefault,
    id: 'program',
    attachTo: {element: '.tour-program', on: 'bottom'},
    title: '!',
    text: ['     .        '],
  },
  {
    ...stepDefault,
    id: 'lessons',
    attachTo: {element: '.tour-lessons', on: 'top'},
    text: ['            .    30 ,      45 .    ,     ,      '],
  },
  {
    ...stepDefault,
    id: 'library',
    attachTo: {element: '.tour-library', on: 'top'},
    text: ['    -.      ,         '],
  },
  {
    ...stepDefault,
    id: 'consult',
    attachTo: {element: '.tour-consult', on: 'top'},
    text: ['            .    :   '],
  },
];

steps = steps.map((step, index) => {
  const stepButtons = [buttons.cancel];
  if (index) stepButtons.push(buttons.back);
  if (index === steps.length - 1) stepButtons.push(buttons.complete);
  else stepButtons.push(buttons.next);
  return {...step, buttons: stepButtons};
});

const tourOptions = {
  defaultStepOptions: {
    cancelIcon: {
      enabled: true
    }
  },
  useModalOverlay: true,
};

const Tour: React.FC = () => {
  return (<>
    { /* @ts-ignore */ }
    <ShepherdTour steps={steps} tourOptions={tourOptions}>
      <TourMethods>
        {(tourContext) => tourContext && <TourStart tour={tourContext}/>}
      </TourMethods>
    </ShepherdTour>
  </>)
}

export default Tour;